/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
/* eslint-disable @typescript-eslint/no-explicit-any */
import React from "react";
import { Tabs, Icon, Button } from "antd-mobile";
import EasyNarBar from "@/components/EasyNarBar";
import PerformListView from "./PerformListView";

class PerformanceManagement extends React.Component<any, any> {
  state: any;
  props: any;
  tabs: any;
  constructor(props: any) {
    super(props);
    this.state = {
      tabs: [{ title: `待办理(${0})` }, { title: `已办理` }, { title: `已失效` }],
      TabIndex: 0
    };
  }
  componentDidMount() {
    const { state } = this.props.location;
    if (state) {
      const { TabIndex } = state;
      this.setState({
        TabIndex: Number(TabIndex) || 0
      });
    }
  }
  setWaitTodoLength = (length: number) => {
    this.setState({
      tabs: [{ title: `待办理(${length})` }, { title: `已办理` }, { title: `已失效` }]
    });
  };
  onTabClick = (tab: any, index: number) => {
    this.setState({ TabIndex: index });
  };
  render() {
    return (
      <div style={{ backgroundColor: "#fff" }}>
        <EasyNarBar
          mainContent="履职管理"
          leftContent={<Icon type="left" />}
          onLeftClick={() => {
            this.props.history.push({ pathname: "/dashboard" });
          }}
          rightContent={
            <span
              onClick={() => {
                this.props.history.push({ pathname: "/perform_count" });
              }}
              style={{ fontSize: "15px" }}
            >
              履职统计
            </span>
          }
        ></EasyNarBar>
        <Tabs tabs={this.state.tabs} initialPage={0} page={this.state.TabIndex} onTabClick={this.onTabClick} swipeable={true}>
          <PerformListView key={`PerformListView-0`} status="0" setLenth={this.setWaitTodoLength} {...this.props}></PerformListView>
          <PerformListView key={`PerformListView-1`} status="1" {...this.props}></PerformListView>
          <PerformListView key={`PerformListView-2`} status="2" {...this.props}></PerformListView>
        </Tabs>
      </div>
    );
  }
}
export default PerformanceManagement;
