import React, { useMemo, useState, useEffect } from "react";
import { ListView, PullToRefresh } from "antd-mobile";

type IListViewProps = {
  renderRow: any;
  separator?: any;
  pageSize: number;
  onEndReached: Function;
  height: number;
  isLoading: boolean;
  dataSource: any;
  hasMore: boolean;
  onRefresh?: Function;
};

const ListViewComponent = (props: IListViewProps) => {
  const [dataSource, setdataSource] = useState(
    new ListView.DataSource({
      rowHasChanged: (row1: any, row2: any) => row1 !== row2,
    })
  );

  const onEndReached = (event: any) => {
    if (hasMore && !isLoading) {
      props.onEndReached(event);
    }
  };

  const onRefresh = () => {
    props.onRefresh(event);
  };

  useEffect(() => {
    setdataSource(dataSource.cloneWithRows(props.dataSource));
  }, [props.dataSource]);

  const { hasMore, isLoading } = props;
  return useMemo(
    () => (
      <ListView
        dataSource={dataSource}
        renderFooter={() => (
          <div style={{ padding: 30, textAlign: "center" }}>
            {!hasMore ? "没有更多数据了" : isLoading ? "正在加载中..." : ""}
          </div>
        )}
        style={{
          height: props.height,
        }}
        renderRow={props.renderRow}
        renderSeparator={props.separator}
        className="am-list"
        pageSize={props.pageSize}
        scrollRenderAheadDistance={500}
        onEndReached={onEndReached}
        onEndReachedThreshold={10}
        pullToRefresh={
          props.onRefresh && <PullToRefresh refreshing={false} onRefresh={onRefresh} />
        }
      />
    ),
    [hasMore, isLoading, dataSource]
  );
};

export default ListViewComponent;
