import React from "react";
// import { ListView } from "antd-mobile";
import { observer, inject } from "mobx-react";

import ListView from "@/components/ListView/listview";
import PerformItem from "./PerformItem";

import DateRangeSelect from "@/components/DateRangeSelect";
import PerformListViewStore from "./store";
require("./style.scss");

@observer
class PerformListView extends React.Component<any, any> {
  store: any;
  constructor(props: any) {
    super(props);
    this.state = {
      height: 1
    };
    this.store = new PerformListViewStore(this.props);
  }
  componentDidMount() {
    /*
    const { initStore } = this.props.store;
    this.store = new PerformListViewStore(this.props.status)
    initStore(this.props.status);
    */
    const dateTop = document.querySelector(".dateRange-select-wrap");
    const NarBar = document.querySelector(".am-navbar");
    const TabsTop = document.querySelector(".am-tabs-default-bar-content");
    let Height = Math.max(document.documentElement.clientHeight, document.body.clientHeight);
    Height = Height - (dateTop ? dateTop.offsetHeight : 0) - (NarBar ? NarBar.offsetHeight : 0) - (TabsTop ? TabsTop.offsetHeight : 0);
    this.setState({
      height: Height
    });
  }
  onEndReached = () => {
    const { isLoading, hasMore, performFetch } = this.store;
    if (!isLoading && hasMore) {
      performFetch();
    }
  };
  onSearch = (rangeTime: [Date, Date]) => {
    this.store.onSearch(rangeTime);
  };
  separator = (sectionID: string, rowID: string) => (
    <div
      key={`${sectionID}-${rowID}`}
      style={{
        backgroundColor: "#F5F5F9",
        width: "100%",
        height: "0",
        borderBottom: "1px solid #DCDCDC"
      }}
    />
  );
  renderRow = (rowData: any) => {
    return <PerformItem rowData={rowData} status={this.props.status} history={this.props.history}></PerformItem>;
  };
  render() {
    const { dataSource, isLoading, hasMore, refreshing, onRefresh } = this.store;
    return (
      <div>
        <DateRangeSelect label="任务日期" onChange={this.onSearch} />
        <ListView
          dataSource={dataSource}
          renderRow={this.renderRow}
          separator={this.separator}
          pageSize={20}
          onEndReached={this.onEndReached}
          height={this.state.height}
          isLoading={isLoading}
          hasMore={hasMore}
          onRefresh={onRefresh}
        />
      </div>
    );
  }
}
export default PerformListView;
