import React from "react";
import { Icon } from "antd-mobile";

type IProps = {
  rowData: {
    view_ExecuteMg_Deal_id: string;
    view_ExecuteMg_Deal_startTime: string;
    view_ExecuteMg_Deal_endTime: string;
    view_ExecuteMg_Deal_handleTime: string;
    view_ExecuteMg_Deal_executeName: string;
  };
  history: any;
  status: string;
};

class PerformItem extends React.PureComponent<IProps> {
  constructor(props: any) {
    super(props);
  }
  linkToDetail = () => {
    this.props.history.push({
      pathname: "/perform_detail",
      state: {
        id: this.props.rowData.view_ExecuteMg_Deal_id,
        status: this.props.status,
        TabIndex: this.props.status
      }
    });
  };

  todoRender = () => (
    <div style={{ backgroundColor: "#fff" }} onClick={this.linkToDetail}>
      <div className="performItem">
        <p>{this.props.rowData.view_ExecuteMg_Deal_executeName}</p>
        <p className="PFdata">履职任务日期: {this.props.rowData.view_ExecuteMg_Deal_startTime}</p>
        <p className="PFdeadline">履职任务完成时限: {this.props.rowData.view_ExecuteMg_Deal_endTime}</p>
        <Icon className="Icon" type="right" color="#BCBCBC"></Icon>
      </div>
    </div>
  );

  completeRender = () => (
    <div style={{ backgroundColor: "#fff" }} onClick={this.linkToDetail}>
      <div className="performItem">
        <p>{this.props.rowData.view_ExecuteMg_Deal_executeName}</p>
        <p className="PFdata">生效日期: {this.props.rowData.view_ExecuteMg_Deal_startTime}</p>
        <p className="PFdeadline">履职日期: {this.props.rowData.view_ExecuteMg_Deal_handleTime}</p>
        <Icon className="Icon" type="right" color="#BCBCBC"></Icon>
      </div>
    </div>
  );

  failureRender = () => (
    <div style={{ backgroundColor: "#fff" }} onClick={this.linkToDetail}>
      <div className="performItem">
        <p>{this.props.rowData.view_ExecuteMg_Deal_executeName}</p>
        <p className="PFdata">生效日期: {this.props.rowData.view_ExecuteMg_Deal_startTime}</p>
        <p className="PFdeadline">失效日期: {this.props.rowData.view_ExecuteMg_Deal_endTime}</p>
        <Icon className="Icon" type="right" color="#BCBCBC"></Icon>
      </div>
    </div>
  );
  render() {
    switch (this.props.status) {
      case "1":
        return this.completeRender();
      case "2":
        return this.failureRender();
      case "0":
      default:
        return this.todoRender();
    }
  }
}
export default PerformItem;
