import React, { useEffect, useMemo, useState } from "react";
import { DatePicker, Icon, Toast } from "antd-mobile";
import dayjs from "dayjs";

require("./style.scss");

interface IProps {
  label: string;
  defaultValue?: [Date, Date];
  onChange: Function;
  minDate?: Date;
  maxDate?: Date;
}

const DateRangeSelect = (props: IProps) => {
  const [startTime, setStartTime] = useState(undefined);
  const [endTime, setEndTime] = useState(undefined);

  const onStartChange = (date: Date) => {
    date = dayjs(date).startOf("day").toDate();
    if (date > endTime) {
      return Toast.info("开始时间需小于结束时间", 1);
    }
    setStartTime(date);
    props.onChange([date, endTime]);
  };

  const onEndChange = (date: Date) => {
    date = dayjs(date).endOf("day").toDate();
    if (startTime > date) {
      return Toast.info("结束时间需大于开始时间", 1);
    }
    setEndTime(date);
    props.onChange([startTime, date]);
  };

  useEffect(() => {
    let defaultStartTime: Date, defaultEndTime: Date;
    if (props.defaultValue) {
      [defaultStartTime, defaultEndTime] = props.defaultValue;
    } else {
      defaultStartTime = dayjs().subtract(1, "month").toDate();
      defaultEndTime = new Date();
    }

    setStartTime(defaultStartTime);
    setEndTime(defaultEndTime);
  }, [props.defaultValue]);

  return useMemo(
    () => (
      <div className="dateRange-select-wrap">
        <div className="dateRange-select">
          <div className="dateRange-select-label">{props.label}</div>
          <div className="dateRange-select-value">
            <DatePicker mode="date" title="选择开始时间" value={startTime} onChange={onStartChange} maxDate={props.maxDate} minDate={props.minDate}>
              <div className="dateRange-select-content">
                <span>{(startTime && dayjs(startTime).format("YYYY-MM-DD")) || "开始时间"}</span>
              </div>
            </DatePicker>
            <span>至</span>
            <DatePicker mode="date" title="选择结束时间" value={endTime} onChange={onEndChange} maxDate={props.maxDate} minDate={props.minDate}>
              <div className="dateRange-select-content">
                <span>{(endTime && dayjs(endTime).format("YYYY-MM-DD")) || "结束时间"}</span>
              </div>
            </DatePicker>
          </div>
        </div>
      </div>
    ),
    [startTime, endTime, props.maxDate, props.minDate]
  );
};

export default DateRangeSelect;
